import React, { useEffect, useState } from 'react';
import { useAuth } from '../contexts/AuthContext';

// Components
import Registrar from './Registrar';

const Identificacion = (props) => {
    const { validateDoc } = useAuth();
    const [ data, setData] = useState(null);
    const [ loading, setLoading] = useState(true);

    useEffect(async () => {
        await validateDoc(props.documento).then((querySnapshot) => {
            if(querySnapshot.size){
                setData({...querySnapshot.docs[0].data(), doc: querySnapshot.docs[0].id})
            }

            setLoading(false)
        })
        return () => {
        }
    }, [])

    const handleBack = () => {
        props.setDocumento(null);
    }

    return(
        <div>
            { (loading) && <h1>Loading...</h1> }

            { (!data && !loading) && <h1>No eres asociado no puedes registrarte en el sistema</h1>}

            { (data && !data.estado && !loading) && <h1>Eres un asociado inactivo contactanos para poder ayudarte.</h1>}

            { (data && data.usuario !== null && data.estado && !loading) && <h1>Ya tienes una cuenta registrada...</h1>}

            { (data && data.usuario === null && data.estado) && <Registrar asociado={data}/> }
        </div>
    )
}

export default Identificacion;